body {
  margin: 0;
  overflow: hidden;
}

.header {
  padding: 10px;
  background-color: rgb(211, 81, 81);
  color: #fff;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
}

.header h1 {
  font-size: 25px;
  padding: 0;
  margin: 0;
  text-align: center;
}

.map {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.volcano-info-box {
  top: 100%;
  right: 100%;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: #fff;
}

.volcano-info-box ul {
  list-style-type: none;
  padding: 0;
}

.volcano-icon {
  font-size: 2rem;
  color: red;
}